@import url(https://fonts.googleapis.com/css2?family=Varela&display=swap);
a.home-option{color:#1A1A1A;text-decoration:inherit;display:flex}a.home-option svg{padding:2px 5px 0 5px}

.kanever-logo svg{width:280px;height:280px;border-radius:50%;background-color:white}.kanever-logo .st0{fill:#AAAAAA}.kanever-logo .st1{fill:#FFFFFF}.kanever-logo .st2{fill:#E8C52E}

.home{background:#FFD029;height:1080px;display:flex;flex-direction:column;align-items:center}.home .logo{margin-top:40px;margin-bottom:80px}.home h1{color:white;margin-top:100px}.home div:last-child{display:flex;justify-content:space-between;align-self:stretch;margin:0 13%}@media (max-width: 480px){.home h1{margin-top:70px}.home .logo{margin-top:25px;margin-bottom:50px}.home div:last-child{flex-direction:column;justify-content:left;grid-gap:20px;gap:20px}}

.polygon svg{padding-top:10px}

.bordered-text-box{color:#939191;max-width:427px;min-height:100px;padding:20px;padding-bottom:0;border:4px solid #FFD029;margin:-4px 0 0 -4px}.bordered-text-box div{border:4px solid #EDEDED;padding:8px 70px 35px 8px;min-height:100px;margin:-5px -50px -20px -5px}.bordered-text-box div p{margin:0}@media (max-width: 705px){.bordered-text-box div{margin-right:0;padding-right:0}}

.progress-indicator-wrapper{display:flex;align-items:center;color:#939191}.progress-indicator-wrapper .progress-indicator{border-radius:25px;border:4px solid #FFD029;min-width:350px;height:30px;margin-right:20px;display:flex}.progress-indicator-wrapper .progress-indicator>div{margin:3.5px;background-color:#DCDBD6;border-radius:25px}@media (max-width: 546px){.progress-indicator-wrapper .progress-indicator{min-width:180px;margin-right:10px}}

.about{display:flex;flex-direction:column}.about .main-about{padding-left:25%}.about .main-about .title{display:flex}.about .main-about .title>span{align-self:flex-end;height:15px;margin-right:-12px;margin-left:12px;border-left:4px solid #FFD029}.about .main-about .title>.polygon{margin-right:5px;margin-left:-5px}.about .main-about .description{border-left:4px solid #FFD029;border-top:4px solid #FFD029;margin-left:12px}.about .main-about .description .secondary-text{padding-left:30px;padding-top:30px;max-width:427px;min-height:300px}.about .main-about .description .secondary-text>p{margin-bottom:0px}.about .main-about .knowledge-title{display:flex}.about .main-about .knowledge-title>span:nth-of-type(1){border-left:4px solid #FFD029;border-bottom:4px solid #FFD029;min-width:15%;margin-right:-2px;margin-left:12px}.about .main-about .knowledge-title>span:nth-of-type(2){border-bottom:4px solid #EDEDED;flex-grow:1}.about .main-about .knowledge-title>div{display:flex;align-items:center;margin-bottom:-17px}.about .main-about .knowledge-title>div>h3{padding:0 10px}.about>span{border-top:4px solid #EDEDED;max-width:75%;margin-top:-110px;margin-bottom:110px}.about .knowledge-wrapper{margin-top:100px;padding-left:19%;display:flex}.about .knowledge-wrapper>div:nth-child(1){display:flex;flex-direction:column}.about .knowledge-wrapper>div:nth-child(1)>div{margin-bottom:55px}@media (max-width: 700px){.about .main-about{padding-left:8%;padding-right:3%}.about .knowledge-title>span:nth-of-type(1){min-width:5%}.about>span{margin-top:-40px;margin-bottom:40px}.about .knowledge-wrapper{padding-left:5%}}

.video{border:4px solid #FFD029}.video iframe{margin-bottom:-30px;margin-right:-30px;padding:10px}@media (min-width: 514px){iframe{width:448px;height:252px}}

.external-videos{display:flex;flex-wrap:wrap;justify-content:center;grid-gap:30px;gap:30px}

.portfolio{display:flex;flex-direction:column;padding-top:5px}.portfolio>div:nth-of-type(1){display:flex;align-items:center;justify-content:flex-end;margin-bottom:40px}.portfolio>div:nth-of-type(1)>span:nth-of-type(1){border:4px solid #FFD029;height:48px;width:300px;margin-left:4px}.portfolio>div:nth-of-type(1)>span:nth-of-type(2){border-bottom:4px solid #FFD029;flex-grow:1;max-width:18%}.portfolio>.polygon{padding-left:5px}.portfolio .title{display:flex;align-items:center;justify-content:flex-end}.portfolio .title>span{border-bottom:4px solid #FFD029;flex-grow:1;padding-top:5px;margin-left:-2px;max-width:25%}.portfolio .subtitle{display:flex;align-items:center;justify-content:flex-end;padding-right:24.8%}.portfolio .video-filter{display:flex;align-items:center;flex-wrap:wrap-reverse}.portfolio .video-filter>span:nth-of-type(1){border-bottom:4px solid #DCDBD6;color:#939191;margin-top:-12px;margin-bottom:12px;text-align:center;min-width:20%}.portfolio .video-filter>span:nth-of-type(2){border-left:4px solid #DCDBD6;height:82px}.portfolio .video-filter ul{height:80%;padding:0;list-style-type:none;min-width:247px}.portfolio .video-filter ul>li{display:flex;align-items:center}.portfolio .video-filter ul>li>span{border-bottom:4px solid #DCDBD6;min-width:40px;margin-right:-4px}.portfolio .video-filter ul>li>.polygon svg{padding-top:4px;padding-right:5px}.portfolio .video-filter .portfolio-description{margin:0 5px;text-align:right;border:4px solid #DCDBD6;flex:0 1 455px;padding:10px 40px 0 0}.portfolio>span{border:4px solid #FFD029;height:150px}@media (max-width: 450px){.portfolio .subtitle{padding-right:0;align-self:center}}


.content{font-size:18px}.content-wrap{width:85%;margin:0 auto}

html{scroll-behavior:smooth}body{margin:0;font-family:'Varela', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}h1{font-size:48px;font-weight:400;line-height:1;margin:0}h3{font-size:24px;font-weight:400;line-height:1;margin:0}section{margin-bottom:80px}p{color:#939191}

